import React, { Component } from 'react';
import api from '../../lib/api';
import NavBar from '../NavBar';
import ProgressBar from '../ProgressBar';

class ReportUpdate extends Component {
  constructor(props) {
    super(props);

    this.state = {
      user: props.user,
      act: {
        id: props.match.params.id
      },
      isLoaded: false,
      isProcess: false
    }
  }

  componentDidMount() {
    const token = this.state.user.token;
    const id = this.state.act.id;
    //api.getReportById(token, report_id)
    api.act_01.getById(token, id)
      .then(act => {
        if (act) {
          this.setState({ ...this.state, act, isLoaded: true });
        }
        else {
          this.props.showWarningAlert('  ');
          this.props.history.goBack();
        }
      })
      .catch(({ error }) => {
        this.props.showWarningAlert(error.message);
        this.props.history.goBack();
      });
  }

  handleUpdateReport = value => {
    const { act } = this.state;
    if (Number.isNaN(value) || value < 0) {
      return this.props.showWarningAlert('   ');
    }
    if ((act.last_value !== null || act.last_value !== undefined) && (value < Number(act.last_value))) {
      return this.props.showWarningAlert('   ');
    }
    const token = this.state.user.token;
    const id = this.state.act.id;
    //api.updateReportById(token, report_id, value)
    this.setState({...this.state, isProcess: true});
    api.act_01.updateById(token, id, value)
      .then(result => {
        if (!result || !result.done) {
          this.setState({...this.state, isProcess: false}, () => {
            this.props.showWarningAlert('  ');
          });
        }
        else {
          //api.sendReport(token, report_id)
          api.act_01.sendEmailById(token, id)
            .then(done => {
              this.setState({...this.state, isProcess: false}, () => {
                this.props.showSuccessAlert('   .');
                this.props.history.goBack();
              });
            })
            .catch(({ error }) => {
              this.setState({...this.state, isProcess: false}, () => {
                this.props.showWarningAlert(" ,   . \n" + error.message);
                this.props.history.goBack();
              });
            });
        }

        // if (result && result.done) {
        //   this.props.showSuccessAlert('  ');
        //   this.props.history.goBack();
        // }
        // else {
        //   this.props.showWarningAlert('  ');
        // }
      })
      .catch(({ error }) => {
        this.setState({...this.state, isProcess: false}, () => {
          this.props.showWarningAlert(error.message);
        });
      });
  }

  render() {
    if (!this.state.isLoaded) return null;
    const { act } = this.state;
    let val;
    const onClickSave = e => {
      e.preventDefault();
      this.handleUpdateReport(val.value);
    };
    return (
      <div className="container">
        <NavBar {...this.props} />
        <div className="row justify-content-center">
          <div className="col-12 col-sm-12 col-md-8 col-md-offset-2 col-lg-6 col-lg-offset-3">
            <div className="text-center font-weight-bold mb-5">
              <h3> </h3>
            </div>
            <form>
              <div className="form-group">
                <input className="form-control" type="number" placeholder=" " required ref={r => val = r} defaultValue={act.current_value} />
              </div>
              <div className="form-group">
                {
                  this.state.isProcess ?
                    <ProgressBar message={' ...'} large={true}/> :
                    <button className="btn btn-primary btn-block" onClick={onClickSave}></button>
                }
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
};

export default ReportUpdate;